export class FirebaseAuthError extends Error {
    static errors = {
        EMAIL_EXISTS: {
            email: "       "
        },
        OPERATION_NOT_ALLOWED: {
            password: "        "
        },
        TOO_MANY_ATTEMPTS_TRY_LATER: {
            password: "        .         .    "
        },
        EMAIL_NOT_FOUND: {
            email: "       "
        },
        INVALID_PASSWORD: {
            password: "   "
        },
        USER_DISABLED: {
            email: "    . ,    "
        }
    };

    constructor(message) {
        super(message);
        return this.format();
    }

    format() {
        for (const error in FirebaseAuthError.errors) {
            if (this.message === error) {
                return FirebaseAuthError.errors[error];
            }
        }
    }
}
